*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
  color: #393c41;
}
body{
  width: 100%;
  height: 100vh;
  scroll-behavior: smooth;
  overflow-x: hidden;
}
a{
  text-decoration: none;
  padding: 0 15px;
  font-weight: 600;
}
a:hover{
  background-color: #393c41;
  opacity: 0.1;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 20px;
  color: white;
}